@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap");
* {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
button,
dd {
  margin: 0;
  padding: 0;
}

body {
  line-height: 1.4;
  font-size: 14px;
  font-weight: 500;
  font-family: "Inter", sans-serif;
  color: #292f36;
}

button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

ul {
  list-style: none;
}

.box-content {
  border-radius: 16px;
  background-color: #fffffb;
  box-shadow: 0px 0px 32px 0px rgba(162, 173, 185, 0.2);
  padding: clamp(0.938rem, -1.071rem + 5.357vw, 1.5rem);
}
@media only screen and (max-width: 424px) {
  .box-content {
    border-radius: unset;
    background-color: unset;
    box-shadow: unset;
    padding: 5px;
  }
}

.read-more {
  color: #ff6b6c;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.4285714286;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  transition: color 0.2s ease-in;
  flex-shrink: 0;
}
.read-more:hover {
  color: #c64e4f;
}

.title {
  color: #292f36;
  font-size: clamp(1.125rem, 0.97rem + 0.583vw, 1.25rem);
  font-weight: 600;
  line-height: 1.4;
  letter-spacing: -0.2px;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 80;
  display: none;
}

.close-btn {
  position: absolute;
  top: 1rem;
  right: 0.8rem;
  cursor: pointer;
  z-index: 85;
  color: #ffffff;
  font-size: 18px;
  text-transform: uppercase;
}
@media only screen and (max-width: 374px) {
  .close-btn {
    display: none;
  }
}

.no-scroll {
  overflow-y: hidden;
}

.wrapper {
  display: grid;
  grid-template-columns: 240px 1fr;
  height: 100vh;
}
@media only screen and (max-width: 991px) {
  .wrapper {
    display: unset;
  }
}

.gallery-image {
  border-radius: 8px;
  height: 0;
  position: relative;
  overflow: hidden;
}
.gallery-image > * {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.gallery-image--full {
  padding-bottom: 34.25%;
}
.gallery-image--half {
  padding-bottom: 48.25%;
}

.sidebar {
  padding: 23px 16px 0 16px;
  background-color: #fffffb;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  position: relative;
  z-index: 99;
  height: 100%;
}
.sidebar-list li {
  align-items: center;
}
.sidebar-list li:not(:last-child) {
  margin-bottom: 8px;
}
.sidebar-list li a {
  text-decoration: none;
  color: #292f36;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.4285714286;
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px 16px;
  transition: all 0.1s linear;
}
@media (hover: hover) {
  .sidebar-list li a:hover {
    background-color: #ffebeb;
    border-radius: 8px;
    color: #ff6b6c;
  }
}
.sidebar-toggle {
  border-radius: 0px 8px 8px 0px;
  background-color: #fffffb;
  padding: 20px 3px;
  position: absolute;
  left: 100%;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  transition: all 0.1s linear;
}
.sidebar-toggle:hover {
  color: white;
  background-color: #ff6b6c;
}
.sidebar-toggle i {
  font-size: 16px;
}
@media only screen and (max-width: 991px) {
  .sidebar {
    padding: 15px 0;
    position: fixed;
    top: 0;
    left: 0;
    width: 300px;
    transition: transform 0.6s cubic-bezier(0.215, 0.61, 0.355, 1);
    transform: translateX(-100%);
    overflow-y: auto;
  }
  .sidebar.active {
    transform: translateX(0);
  }
  .sidebar-list li a {
    font-size: 16px;
  }
}

.header {
  padding: 20px 25px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}
.header-logo {
  display: flex;
  gap: 15px;
  align-items: center;
}
.header-logo__the100 {
  overflow: hidden;
  position: relative;
  text-decoration: none;
}
.header-logo__the100::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.7), transparent);
  transform: skewX(-25deg);
  animation: lightRay 5s infinite;
}
@keyframes lightRay {
  0% {
    left: -100%;
  }
  100% {
    left: 100%;
  }
}
.header-logo__bars {
  display: none;
}
.header-logo__bars img {
  max-width: 40px;
}
.header-search {
  position: relative;
  border-radius: 6px;
  border: 1px solid #a2adb9;
  background-color: #fffffb;
  display: flex;
  align-items: center;
  width: clamp(30rem, -0.909rem + 48.295vw, 35.313rem);
  height: 45px;
}
.header-search__wrapper {
  padding: 12px 16px;
}
.header-search__dropdown {
  flex-shrink: 0;
  border-right: 1.8px solid #a2adb9;
  padding-right: 16px;
}
.header-search__dropdown button {
  display: flex;
  gap: 10px;
  align-items: center;
  color: #292f36;
  font-size: 14px;
  line-height: 20/14;
  font-weight: 400;
}
.header-search__dropdown ul {
  position: absolute;
  top: 100%;
  left: 0;
  width: 28%;
  background-color: #fffffb;
  border-radius: 6px;
  border: 1px solid #a2adb9;
  padding: 8px 16px;
  z-index: 1;
  display: none;
}
.header-search__dropdown ul li a {
  text-decoration: none;
  color: #292f36;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.4285714286;
  display: block;
  padding: 10px 0;
}
.header-search__field {
  font-family: inherit;
  outline: none;
  border: none;
  color: #a2adb9;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.4285714286;
}
.header-search__btn {
  background: #be232b;
  height: 100%;
  width: 50px;
  border-radius: 0 6px 6px 0;
  flex-shrink: 0;
  transition: opacity 0.1s linear;
}
.header-search__btn:hover {
  opacity: 0.8;
}
.header-account {
  display: flex;
  gap: 16px;
}
.header-account__btn {
  font-size: 14px;
  font-weight: 600;
  line-height: 1.4285714286;
  padding: 12px 24px;
  border-radius: 6px;
  border: 1px solid #ff6b6c;
  background-color: transparent;
  transition: opacity 0.2s linear;
}
.header-account__btn:hover {
  opacity: 0.8;
}
.header-account__btn--login {
  color: #fffffb;
  background-color: #ff6b6c;
}
.header-account__btn--register {
  color: #ff6b6c;
}
@media only screen and (max-width: 991px) {
  .header-logo__bars {
    display: block;
  }
}
@media only screen and (max-width: 599px) {
  .header-search {
    width: 100%;
  }
  .header-search__dropdown ul {
    width: 100%;
  }
}

.card-item {
  border-radius: 16px;
  width: 100%;
  padding: 24px;
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}
.card-item--first {
  background-image: url("../../assets/images/card-item1.svg");
}
.card-item--second {
  background-image: url("../../assets/images/card-item2.svg");
}
.card-item--third {
  background-image: url("../../assets/images/card-item3.svg");
}
.card-item--fourth {
  background-image: url("../../assets/images/card-item4.svg");
}
.card-item__content {
  display: flex;
  flex-direction: column;
  gap: 20px;
  flex-grow: 1;
}
.card-item__content h2 {
  color: #fffffb;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.4;
  letter-spacing: -0.2px;
}
.card-item__content-btn {
  border-radius: 100rem;
  border: 1px solid #fffffb;
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.3);
  transition: opacity 0.1s linear;
}
.card-item__content-btn:hover {
  opacity: 0.8;
}

.product-title {
  color: #292f36;
  font-size: clamp(1.125rem, 0.66rem + 1.749vw, 1.5rem);
  font-weight: 600;
  line-height: 1.3333333333;
}
.product-list {
  --col: 5;
  display: grid;
  grid-template-columns: repeat(var(--col), minmax(0, 1fr));
  gap: clamp(0.938rem, -0.577rem + 1.731vw, 1.5rem);
  flex-wrap: wrap;
}
.product-item {
  border-radius: 8px;
  border: 1px solid #ffebeb;
  background: #fffffb;
  box-shadow: 0px 0px 32px 0px rgba(162, 173, 185, 0.2);
  padding: 16px;
}
.product-item__image {
  position: relative;
  border-radius: 8px;
  overflow: hidden;
}
.product-item__image img {
  transition: all 0.2s linear;
  object-fit: cover;
}
@media (hover: hover) {
  .product-item__image:hover > img {
    transform: scale(1.2);
    opacity: 0.9;
  }
}
.product-item__image-award {
  position: absolute;
  left: 0;
  top: 0;
  margin: 12px;
  padding: 8px;
  z-index: 10;
  border-radius: 8px;
  border: 1px solid #fffffb;
  background: rgba(255, 255, 251, 0.3);
  backdrop-filter: blur(20px);
  width: 40px;
  height: 40px;
}
.product-item__image-award img {
  width: 24px;
  height: 24px;
}
.product-item__content {
  padding-top: 12px;
}
.product-item__content h4 a {
  color: #5b5f97;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.5;
  letter-spacing: -0.16px;
  text-decoration: none;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  word-break: break-word;
}
.product-item__statistic {
  display: grid;
  gap: 16px;
  grid-template-columns: repeat(2, 1fr);
  margin-top: 8px;
}
.product-item__statistic-col {
  color: #5b5f97;
}
.product-item__statistic-col span {
  font-size: 12px;
  font-weight: 500;
  line-height: 1.3333333333;
  letter-spacing: -0.06px;
  margin-bottom: 4px;
  display: block;
}
.product-item__statistic-col b {
  font-size: 14px;
  font-weight: 600;
  line-height: 1.4285714286;
}
@media only screen and (max-width: 1400px) {
  .product-list {
    --col: 3;
  }
}
@media only screen and (max-width: 1023px) {
  .product-list {
    --col: 2;
  }
}
@media only screen and (max-width: 599px) {
  .product-list {
    display: grid;
    justify-content: flex-start;
    grid-auto-columns: 80%;
    grid-auto-flow: column;
    grid-gap: 1rem;
    overflow: auto;
    overflow-y: hidden;
    scroll-snap-type: x mandatory;
    scroll-snap-stop: always;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    -webkit-overflow-scrolling: touch;
    scroll-padding: 1rem;
    grid-template-columns: unset;
  }
  .product-list::-webkit-scrollbar {
    display: none;
    width: 0;
  }
  .product-list > * {
    scroll-snap-align: start;
  }
  .product-item {
    padding: 0;
  }
  .product-item__statistic {
    grid-template-columns: 1fr;
    gap: 8px;
  }
  .product-item__statistic-col {
    display: flex;
    align-items: center;
    gap: 8px;
  }
  .product-item__statistic-col span {
    margin-bottom: unset;
  }
  .product-item__image {
    border-radius: 8px 8px 0 0;
  }
  .product-item__content {
    padding: 14px;
  }
}

.table-list {
  width: 100%;
  border-collapse: collapse;
}
.table-list__rank {
  border-radius: 8px;
  background-color: #ffebeb;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  backdrop-filter: blur(20px);
  width: 28px;
  height: 28px;
  color: #ff6b6c;
  font-size: 12px;
  font-weight: 700;
  line-height: 1.3333333333;
  letter-spacing: -0.06px;
  flex-shrink: 0;
}
.table-list__image {
  width: 48px;
  height: 48px;
  object-fit: cover;
}
.table-list__image--first {
  border-radius: 8px;
}
.table-list__image--second {
  border-radius: 100rem;
}
.table-list__package {
  font-size: 12.8px;
  font-weight: 600;
  line-height: 1.3333333333;
  padding: 5px;
  display: inline-block;
  border-radius: 4px;
  transition: all 0.1s linear;
}
.table-list__package--vip {
  color: #ff6b6c;
  background: #ffebeb;
}
.table-list__package--normal {
  background: #feefdd;
  color: #ffc145;
}
.table-list thead tr,
.table-list tbody tr {
  display: grid;
  align-items: center;
  grid-template-columns: 2.5fr 1fr 1fr;
  grid-gap: 20px;
}
.table-list thead th,
.table-list tbody td {
  text-align: left;
}
.table-list thead {
  margin-bottom: 10px;
  display: block;
}
.table-list thead tr th {
  color: #5b5f97;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.3333333333;
  letter-spacing: -0.06px;
  padding: 0 12px;
}
.table-list tbody tr {
  transition: all 0.2s linear;
  background-color: #ffffff;
  border-radius: 10px;
}
.table-list tbody tr:not(:last-child) {
  margin-bottom: 12px;
}
.table-list tbody tr td {
  color: #5b5f97;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.5;
  letter-spacing: -0.16px;
  padding: 14px 12px;
  display: flex;
  align-items: center;
  gap: 16px;
}
@media (hover: hover) {
  .table-list tbody tr:hover {
    background-color: #ffebeb;
  }
  .table-list tbody tr:hover td {
    color: #ff6b6c;
  }
  .table-list tbody tr:hover .table-list__rank {
    background: #ffffff;
    transition: background 0.2s linear;
  }
  .table-list tbody tr:hover .table-list__package--vip, .table-list tbody tr:hover .table-list__package--normal {
    background: transparent;
  }
}
@media only screen and (max-width: 599px) {
  .table-list thead {
    display: none;
  }
  .table-list tbody,
.table-list tbody tr,
.table-list tbody td {
    display: block;
  }
  .table-list tbody tr {
    background-color: #f5f2f2;
  }
  .table-list tbody tr:before {
    content: attr(data-label);
    font-weight: bold;
    background: #ff6b6c;
    color: #ffffff;
    font-size: 14px;
    width: 28px;
    height: 28px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    border-radius: 6px;
  }
  .table-list tbody tr:not(:nth-child(9999)) {
    margin-bottom: 1.5rem;
  }
  .table-list tbody tr td {
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 0;
  }
  .table-list tbody tr td:before {
    content: attr(data-label);
    float: left;
    font-weight: bold;
  }
  .table-list__image {
    margin-right: auto;
  }
  .table-list__rank {
    display: none;
  }
}